
const SCROLL_TYPE = {
  H_LEFT: Symbol(''),
  H_RIGHT: Symbol(''),
  V_TOP: Symbol(''),
  V_BOTTOM: Symbol(''),
  UN: Symbol(''),
};

class Scroll {

  constructor(table) {
    this.table = table;
    this.x = 0;
    this.y = 0;
    this.ri = table.fixed.fxTop + 1;
    this.ci = table.fixed.fxLeft + 1;
    this.type = SCROLL_TYPE.UN;
  }
}

export { Scroll, SCROLL_TYPE };
